'use client'
// pages/design-idea/[slug].tsx
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  ArrowLeft, 
  Heart, 
  Share2, 
  MapPin, 
  Clock, 
  Maximize2, 
  Palette, 
  Star,
  ChevronLeft,
  ChevronRight,
  X,
  Phone,
  Mail,
  Calendar
} from 'lucide-react';
import { designAPI, Design } from '../../../lib/api';
import Image from 'next/image';
import Link from 'next/link';
import Head from 'next/head';

const DesignDetailPage = () => {
  const router = useRouter();
  const { slug } = router.query;
  
  const [design, setDesign] = useState<Design | null>(null);
  const [relatedDesigns, setRelatedDesigns] = useState<Design[]>([]);
  const [loading, setLoading] = useState(true);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [showGallery, setShowGallery] = useState(false);
  const [isFavorite, setIsFavorite] = useState(false);
  const [showContactForm, setShowContactForm] = useState(false);

  useEffect(() => {
    if (slug) {
      fetchDesign(slug as string);
    }
  }, [slug]);

  const fetchDesign = async (designSlug: string) => {
    try {
      setLoading(true);
      // Get design by searching with slug
      const searchResult = await designAPI.searchDesigns({ q: designSlug });
      const foundDesign = searchResult.data?.find((d: Design) => d.slug === designSlug);
      
      if (foundDesign) {
        setDesign(foundDesign);
        // Fetch related designs based on categories
        if (foundDesign.categories && foundDesign.categories.length > 0) {
          const categorySlug = foundDesign.categories[0].slug;
          const relatedResult = await designAPI.getDesignsByCategory(categorySlug);
          const filtered = relatedResult.data?.filter((d: Design) => d.id !== foundDesign.id) || [];
          setRelatedDesigns(filtered.slice(0, 6));
        }
      } else {
        router.push('/404');
      }
    } catch (error) {
      console.error('Error fetching design:', error);
      router.push('/404');
    } finally {
      setLoading(false);
    }
  };

  const getImageUrl = (imageUrl: string) => {
    if (imageUrl.startsWith('http')) return imageUrl;
    return `${process.env.NEXT_PUBLIC_API_URL?.replace('/api', '') || 'http://localhost:1337'}${imageUrl}`;
  };

  const getAllImages = () => {
    if (!design) return [];
    const images = [];
    if (design.featured_image) {
      images.push(design.featured_image);
    }
    if (design.images) {
      images.push(...design.images.filter(img => img.id !== design.featured_image?.id));
    }
    return images;
  };

  const toggleFavorite = () => {
    setIsFavorite(!isFavorite);
    // Here you can add logic to save to localStorage or API
  };

  const shareDesign = async () => {
    if (navigator.share && design) {
      try {
        await navigator.share({
          title: design.title,
          text: design.description || '',
          url: window.location.href,
        });
      } catch (error) {
        // Fallback to copying URL
        navigator.clipboard.writeText(window.location.href);
        alert('Link copied to clipboard!');
      }
    } else {
      navigator.clipboard.writeText(window.location.href);
      alert('Link copied to clipboard!');
    }
  };

  const nextImage = () => {
    const images = getAllImages();
    if (currentImageIndex < images.length - 1) {
      setCurrentImageIndex(currentImageIndex + 1);
    }
  };

  const prevImage = () => {
    if (currentImageIndex > 0) {
      setCurrentImageIndex(currentImageIndex - 1);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!design) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">Design Not Found</h1>
          <Link
            href="/design-ideas"
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          >
            Browse All Designs
          </Link>
        </div>
      </div>
    );
  }

  const allImages = getAllImages();
  const currentImage = allImages[currentImageIndex];

  return (
    <>
      <Head>
        <title>{design.title} - Interior Design Ideas</title>
        <meta name="description" content={design.description || `Beautiful ${design.style} design in ${design.location}`} />
        <meta property="og:title" content={design.title} />
        <meta property="og:description" content={design.description || ''} />
        <meta property="og:image" content={design.featured_image ? getImageUrl(design.featured_image.url) : ''} />
      </Head>

      <div className="min-h-screen bg-gray-50">
        {/* Header */}
        <div className="bg-white shadow-sm sticky top-0 z-40">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex items-center justify-between py-4">
              <button
                onClick={() => router.back()}
                className="flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors"
              >
                <ArrowLeft size={20} />
                <span className="hidden sm:inline">Back</span>
              </button>

              <div className="flex items-center gap-3">
                <button
                  onClick={toggleFavorite}
                  className={`p-2 rounded-full transition-colors ${
                    isFavorite 
                      ? 'bg-red-100 text-red-600' 
                      : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                  }`}
                >
                  <Heart size={20} className={isFavorite ? 'fill-current' : ''} />
                </button>

                <button
                  onClick={shareDesign}
                  className="p-2 rounded-full bg-gray-100 text-gray-600 hover:bg-gray-200 transition-colors"
                >
                  <Share2 size={20} />
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Main Content */}
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            
            {/* Left Column - Images */}
            <div className="lg:col-span-2">
              {/* Main Image */}
              <div className="relative mb-6">
                <div className="relative aspect-[4/3] bg-gray-100 rounded-xl overflow-hidden">
                  {currentImage && (
                    <Image
                      src={getImageUrl(currentImage.url)}
                      alt={currentImage.alternativeText || design.title}
                      fill
                      className="object-cover cursor-pointer"
                      onClick={() => setShowGallery(true)}
                    />
                  )}
                  
                  {/* Navigation Arrows */}
                  {allImages.length > 1 && (
                    <>
                      <button
                        onClick={prevImage}
                        disabled={currentImageIndex === 0}
                        className="absolute left-4 top-1/2 -translate-y-1/2 bg-black/50 text-white p-2 rounded-full hover:bg-black/70 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <ChevronLeft size={20} />
                      </button>
                      <button
                        onClick={nextImage}
                        disabled={currentImageIndex === allImages.length - 1}
                        className="absolute right-4 top-1/2 -translate-y-1/2 bg-black/50 text-white p-2 rounded-full hover:bg-black/70 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <ChevronRight size={20} />
                      </button>
                    </>
                  )}

                  {/* Featured Badge */}
                  {design.is_featured && (
                    <div className="absolute top-4 left-4 bg-blue-600 text-white px-3 py-1 rounded-full text-sm font-medium">
                      <Star size={16} className="inline mr-1" />
                      Featured
                    </div>
                  )}

                  {/* Image Counter */}
                  {allImages.length > 1 && (
                    <div className="absolute bottom-4 right-4 bg-black/70 text-white px-3 py-1 rounded-full text-sm">
                      {currentImageIndex + 1} / {allImages.length}
                    </div>
                  )}
                </div>

                {/* Thumbnail Strip */}
                {allImages.length > 1 && (
                  <div className="flex gap-2 mt-4 overflow-x-auto pb-2">
                    {allImages.map((image, index) => (
                      <button
                        key={image.id}
                        onClick={() => setCurrentImageIndex(index)}
                        className={`relative w-20 h-16 bg-gray-100 rounded-lg overflow-hidden flex-shrink-0 border-2 transition-colors ${
                          index === currentImageIndex ? 'border-blue-500' : 'border-transparent'
                        }`}
                      >
                        <Image
                          src={getImageUrl(image.url)}
                          alt={`View ${index + 1}`}
                          fill
                          className="object-cover"
                        />
                      </button>
                    ))}
                  </div>
                )}
              </div>

              {/* Description */}
              {design.description && (
                <div className="bg-white rounded-xl p-6 shadow-sm">
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">About This Design</h3>
                  <p className="text-gray-600 leading-relaxed">{design.description}</p>
                </div>
              )}
            </div>

            {/* Right Column - Details */}
            <div className="lg:col-span-1">
              <div className="bg-white rounded-xl p-6 shadow-sm sticky top-24">
                
                {/* Title and Categories */}
                <div className="mb-6">
                  <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-3">
                    {design.title}
                  </h1>
                  
                  {design.categories && design.categories.length > 0 && (
                    <div className="flex flex-wrap gap-2 mb-4">
                      {design.categories.map(category => (
                        <Link
                          key={category.id}
                          href={`/design-ideas?category=${category.slug}`}
                          className="px-3 py-1 bg-blue-100 text-blue-700 text-sm rounded-full hover:bg-blue-200 transition-colors"
                        >
                          {category.name}
                        </Link>
                      ))}
                    </div>
                  )}
                </div>

                {/* Price */}
                {design.price_range && (
                  <div className="flex items-center gap-2 mb-6 p-4 bg-green-50 rounded-lg">
                    <div>
                      <p className="text-sm text-green-600">Price Range</p>
                      <p className="text-xl font-bold text-green-700">{design.price_range}</p>
                    </div>
                  </div>
                )}

                {/* Details Grid */}
                <div className="grid grid-cols-2 gap-4 mb-6">
                  {design.location && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <MapPin size={18} />
                      <div>
                        <p className="text-xs text-gray-500">Location</p>
                        <p className="font-medium">{design.location}</p>
                      </div>
                    </div>
                  )}

                  {design.area_size && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <Maximize2 size={18} />
                      <div>
                        <p className="text-xs text-gray-500">Area</p>
                        <p className="font-medium">{design.area_size} sq ft</p>
                      </div>
                    </div>
                  )}

                  {design.style && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <Palette size={18} />
                      <div>
                        <p className="text-xs text-gray-500">Style</p>
                        <p className="font-medium capitalize">{design.style}</p>
                      </div>
                    </div>
                  )}

                  {design.completion_time && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <Clock size={18} />
                      <div>
                        <p className="text-xs text-gray-500">Timeline</p>
                        <p className="font-medium">{design.completion_time}</p>
                      </div>
                    </div>
                  )}
                </div>

                {/* Tags */}
                {design.tags && Array.isArray(design.tags) && design.tags.length > 0 && (
                  <div className="mb-6">
                    <p className="text-sm font-medium text-gray-900 mb-2">Tags</p>
                    <div className="flex flex-wrap gap-2">
                      {design.tags.map((tag, index) => (
                        <span
                          key={index}
                          className="px-2 py-1 bg-gray-100 text-gray-600 text-xs rounded-full"
                        >
                          {tag}
                        </span>
                      ))}
                    </div>
                  </div>
                )}

                {/* CTA Buttons */}
                <div className="space-y-3">
                  <button
                    onClick={() => setShowContactForm(true)}
                    className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 transition-colors font-medium"
                  >
                    Get Quote for This Design
                  </button>
                  
                  <button
                    onClick={() => setShowContactForm(true)}
                    className="w-full border border-blue-600 text-blue-600 py-3 px-4 rounded-lg hover:bg-blue-50 transition-colors font-medium"
                  >
                    Schedule Consultation
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* Related Designs */}
          {relatedDesigns.length > 0 && (
            <div className="mt-16">
              <h2 className="text-2xl font-bold text-gray-900 mb-8">Related Designs</h2>
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                {relatedDesigns.map((relatedDesign) => (
                  <RelatedDesignCard
                    key={relatedDesign.id}
                    design={relatedDesign}
                    getImageUrl={getImageUrl}
                  />
                ))}
              </div>
            </div>
          )}
        </div>

        {/* Gallery Lightbox */}
        <AnimatePresence>
          {showGallery && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 bg-black/90 z-50 flex items-center justify-center p-4"
              onClick={() => setShowGallery(false)}
            >
              <div className="relative max-w-4xl max-h-full" onClick={(e) => e.stopPropagation()}>
                <button
                  onClick={() => setShowGallery(false)}
                  className="absolute -top-12 right-0 text-white hover:text-gray-300 z-10"
                >
                  <X size={24} />
                </button>

                {allImages.length > 1 && (
                  <>
                    <button
                      onClick={prevImage}
                      disabled={currentImageIndex === 0}
                      className="absolute left-4 top-1/2 -translate-y-1/2 text-white hover:text-gray-300 disabled:opacity-50 disabled:cursor-not-allowed z-10"
                    >
                      <ChevronLeft size={32} />
                    </button>
                    <button
                      onClick={nextImage}
                      disabled={currentImageIndex === allImages.length - 1}
                      className="absolute right-4 top-1/2 -translate-y-1/2 text-white hover:text-gray-300 disabled:opacity-50 disabled:cursor-not-allowed z-10"
                    >
                      <ChevronRight size={32} />
                    </button>
                  </>
                )}

                {currentImage && (
                  <motion.img
                    key={currentImage.id}
                    initial={{ scale: 0.8, opacity: 0 }}
                    animate={{ scale: 1, opacity: 1 }}
                    src={getImageUrl(currentImage.url)}
                    alt={currentImage.alternativeText || design.title}
                    className="max-w-full max-h-full object-contain rounded-lg"
                  />
                )}

                {allImages.length > 1 && (
                  <div className="absolute -bottom-12 left-1/2 -translate-x-1/2 text-white text-sm">
                    {currentImageIndex + 1} / {allImages.length}
                  </div>
                )}
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Contact Form Modal */}
        <ContactFormModal
          isOpen={showContactForm}
          onClose={() => setShowContactForm(false)}
          designTitle={design.title}
        />
      </div>
    </>
  );
};

// Related Design Card Component
interface RelatedDesignCardProps {
  design: Design;
  getImageUrl: (url: string) => string;
}

const RelatedDesignCard: React.FC<RelatedDesignCardProps> = ({ design, getImageUrl }) => {
  const imageUrl = design.featured_image?.url || design.images?.[0]?.url;

  return (
    <Link href={`/design-idea/${design.slug}`}>
      <motion.div
        whileHover={{ y: -4 }}
        className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300"
      >
        <div className="relative aspect-[4/3] bg-gray-100">
          {imageUrl ? (
            <Image
              src={getImageUrl(imageUrl)}
              alt={design.title}
              fill
              className="object-cover"
            />
          ) : (
            <div className="w-full h-full flex items-center justify-center text-gray-400">
              No Image
            </div>
          )}
        </div>

        <div className="p-4">
          <h3 className="font-semibold text-gray-900 mb-1 line-clamp-1">
            {design.title}
          </h3>
          
          {design.location && (
            <div className="flex items-center gap-1 text-sm text-gray-500 mb-2">
              <MapPin size={12} />
              {design.location}
            </div>
          )}

          {design.price_range && (
            <div className="text-sm font-semibold text-green-600">
              {design.price_range}
            </div>
          )}
        </div>
      </motion.div>
    </Link>
  );
};

// Contact Form Modal Component
interface ContactFormModalProps {
  isOpen: boolean;
  onClose: () => void;
  designTitle: string;
}

const ContactFormModal: React.FC<ContactFormModalProps> = ({ isOpen, onClose, designTitle }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    message: `I'm interested in the "${designTitle}" design. Please provide more details and a quote.`
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Here you can add API call to send the form data
    console.log('Form submitted:', formData);
    alert('Thank you! We will contact you soon.');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center p-4"
        onClick={onClose}
      >
        <motion.div
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          exit={{ scale: 0.8, opacity: 0 }}
          className="bg-white rounded-xl p-6 w-full max-w-md"
          onClick={(e) => e.stopPropagation()}
        >
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-xl font-bold text-gray-900">Get Quote</h3>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600"
            >
              <X size={20} />
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Name *
              </label>
              <input
                type="text"
                required
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Email *
              </label>
              <input
                type="email"
                required
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Phone
              </label>
              <input
                type="tel"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Message
              </label>
              <textarea
                rows={4}
                value={formData.message}
                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            <div className="flex gap-3 pt-2">
              <button
                type="button"
                onClick={onClose}
                className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
              >
                Cancel
              </button>
              <button
                type="submit"
                className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                Send Inquiry
              </button>
            </div>
          </form>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
};

export default DesignDetailPage;
